* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
body {
  line-height: 1;
  color: #333;
}

header {
  width: 100%;
  height: 70px;
  line-height: 70px; 
  color: #fff;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
}

.main_visual {
  height: 100vh;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}